.social-list {
  width: 100%;
  padding: 19px 0;
  margin: 0;
  list-style-type: none;

  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0 10%;

  border-bottom: 1px solid #d9d9d9;

  @media (min-width: $tablet-width) {
    width: 26%;
    border-bottom: none;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    justify-content: center;
  }
}

.social-list__link {
  display: block;

  &:active {
    opacity: 0.3;
  }
}

.social-list__icon {
  fill: #666666;
}

.social-list__link:hover .social-list__icon {
  fill: #68b738;
}
